import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    language: 'english'
  },
  mutations: {
    setLanguage(state) {
      state.language = state.language === 'english' ? 'swedish' : 'english';
    }
  },
  actions: {
    setLanguage(context) {
      context.commit('setLanguage');
    }
  },
  modules: {
  }
})
